import './styles/main.scss';
import TwitchNote from "./TwitchNote";
import Mouse from "./Mouse";
var ChatContainerClass = 'chat-scrollable-area__message-container';
var twitchNote = new TwitchNote();
var chatExists = false;
var lastUrl = '';
function init() {
    // check for chat container existence
    setInterval(function () {
        // check for URL changes (going through different channels)
        var currentUrl = window.location.href;
        if (lastUrl !== currentUrl) {
            lastUrl = currentUrl;
            chatExists = false;
            return;
        }
        // check for chat container appearing in DOM
        var targetNode = document.querySelector(".".concat(ChatContainerClass));
        if (targetNode) {
            if (!chatExists) {
                // chat container appeared
                twitchNote.init(targetNode);
                Mouse.setupListeners();
            }
            chatExists = true;
        }
        else {
            chatExists = false;
        }
    }, 1000);
}
init();
